footer{
    width:100%;
    height: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
    margin-top:auto;
    background-color:#0d1118;
}

a, a:hover{
    color: white;
    text-decoration: none;
}

.socailmedia{
    width: 100px;
    display: flex;
    justify-content: space-between;
    list-style: none;
    margin-top: 15px;

}



.social{
    width: 25px;
    height: 25px;
    transition: .5s;
}

.social:hover{
    transform: scale(1.2)
}