.ToDoCard{
    width: 500px;
    height: 600px;
    display: flex;
    flex-direction: column;
    background-color: #DBE8E1;
    border-radius: 15px;
    margin-top:30px;
    overflow: auto;
    margin-bottom: 30px;
}

header{
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: #034687;
    color: #041025;
    border-radius: 12px 12px 0 0;
    

}

header h1{
    font-weight: 700;
}

.bt-icon{
    font-size: 2rem;
}

.bt-icon-plus{
    font-size: 3.5rem;
    cursor: pointer;
    transition: .5s;
}

.bt-icon-plus:hover{
    transform: scale(1.5);
}

.cardBody{
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
}

.cardBody::-webkit-scrollbar{
    background: #DBE8E1;
}


.cardBody::-webkit-scrollbar-thumb{
    background: #E10032;
    border-radius:10px;
}

.cardBody::-webkit-scrollbar-thumb:hover{
    background: #b8052c;
}


@media screen and (max-width:504px) {
        .ToDoCard{
            width: 100%;
        }
}