.row{
    width: 460px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content:space-around;
    margin: 20px 0;
    background-color: #CDDFD6;
    box-shadow: 0 4px 11px -8px black;
}

.row h3{
    font-size:1.4rem;
}

.bt-icon-row{
    font-size:1.5rem;
    cursor: pointer;
    transition: .5s;

}

.bt-icon-row:hover{
    transform: scale(1.5);
}

.bt-icon-row:first-child{
    font-size:2rem;
}

input[type=checkbox]{
    display: none;
}


@media  screen and (max-width:504px) {
    .row{
        width: 90%;
    }
}

