.modal {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  transform: translate(0, 0);
  display: none;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.8);
}

.form-modal {
  width: 300px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: #a3b1aa;
  border-radius: 10px;
  box-shadow: 0 4px 15px -5px black;
  animation: modalanimate 0.5s forwards ease-in-out;
}

@keyframes modalanimate {
  from {
    margin-top: -500px;
    opacity: 0;
  }
  to {
    margin-top: initial;
    opacity: initial;
  }
}

.close-button {
  font-size: 2rem;
  color: #e10032;
  position: absolute;
  margin-top: -20px;
  margin-right: -314px;
  cursor: pointer;
  transition: .5s;
}

.close-button:hover{
    transform:scale(1.2)
}

.form-modal h2 {
  padding-top: 15px;
}

.form-button {
  width: 100px;
  height: 40px;
  background-color: #e10032;
  border: none;
  border-radius: 10px;
  box-shadow: 0 4px 10px -3px black;
  margin-bottom: 15px;
  cursor: pointer;
  transition: .5s;
}

.form-button:hover {
  transform: scale(1.2);
}

.form-modal input {
  width: 260px;
  height: 30px;
  border-radius: 10px;
  border: none;
  padding-left: 15px;
  box-shadow: 0 4px 10px -3px black;
}

.form-modal input:focus {
  outline: 2px solid #e10032;
}
